.boardCell{
    width:100px;
    height:100px;

    outline:2px solid #607d8b;
    display: inline-flex;
}
.checked{
    background-image: radial-gradient(circle, #b3e5fc,#29b6f6);
}
.attacked{
    background-image: radial-gradient(circle, #ef9a9a ,darkred);
}
.current{
    background-image: radial-gradient(circle, #fff176 ,#f57f17);
}
.present{
    background-image: radial-gradient(circle, #dce775 ,#43a047);
}
.board{
    font-size: 0;
}

.board-container {
    top: 0;
    height: 87.8vh;
    background: #121419;
}

.items {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

@media only screen and (max-width:1350px) {
    .board-container {
        margin-top: 8rem;
    }
}

@media only screen and (max-width:1000px) {
    .board-container {
        margin-top: 10rem;
    }
}